
body{
  font-family: 'Lucida Sans Unicode';
  background: #4CA1AF;
  background: -webkit-linear-gradient(to left, #4CA1AF, #C4E0E5);
  background: linear-gradient(to left, #4CA1AF, #C4E0E5);
}
button{
  margin: 0 10px 0 10px;
}
form{
  display: flex;
  justify-content: center;
}
h1{
  text-align: center;
  font-size: 50px;
}
p{
  text-align: center;
}
.buttons{
  display: flex;
  justify-content: center;
}
.card{
  height: 150px;
  width: 100px;
  background-color: #BFBFBF;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-red{
  height: 150px;
  width: 100px;
  background-color: #BFBFBF;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
}

.cards{
  margin: 0 auto;
}

.input-bet{
  display: flex;
  justify-content: center;
}